@font-face
{
    font-family: 'Press Start 2P';

    src: local('Press Start 2P'), url('PressStart2P-Regular.ttf');
}
@import url('https://fonts.googleapis.com/css?family=Russo+One');

h1
{
    font-family: 'Press Start 2P', cursive;

    color: #0ccac4;

    fontSize: 4vw;
}
.compName
{
    font-family: 'Press Start 2P', cursive;

    position: absolute;
    left: 0;

    padding-left: 9px;

    color: #0ccac4;
}

p
{
    font-family: 'Russo One', sans-serif;

    background: #0099f7;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #f11712, #0099f7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-background-clip: text;
            background-clip: text;

    -webkit-text-fill-color: transparent;
            text-fill-color: transparent;
}
